<template>
  <h4 class="font-weight-bold">
    {{ deCamelText }}
  </h4>
</template>

<script>
export default {
  props: ["text"],
  computed: {
    deCamelText: function() {
      const toUppercase = str => str.toUpperCase();
      const deCamel = this.text
        .replace(/[A-Z]/g, " $&")
        .replace(/^./, toUppercase);
      return deCamel;
    }
  }
};
</script>
