<template>
  <v-treeview
    class="black--text"
    v-if="items"
    dense
    hoverable
    :items="items"
    activatable
    item-key="id"
    open-on-click
    :search="$store.state.searchTerm"
    :filter="$store.state.searchFilter"
    :open-all="true"
  >
    <template v-slot:prepend="{ item, open }">
      <v-icon v-if="!item.endNode">
        {{ open ? "mdi-folder-open" : "mdi-folder" }}
      </v-icon>
      <v-icon v-else>
        {{ "mdi-file-code-outline" }}
      </v-icon>
    </template>

    <template slot="label" slot-scope="{ item }">
      <div @click="itemSelected(item)">{{ item.name }}</div>
    </template>
  </v-treeview>
</template>

<script>
import store from "../store";
export default {
  computed: {
    items() {
      return this.$store.state.repo ? this.$store.state.repo.items : null;
    }
  },
  store,
  data: () => ({
    files: {
      html: "mdi-language-html5",
      js: "mdi-nodejs",
      json: "mdi-code-json",
      md: "mdi-language-markdown",
      pdf: "mdi-file-pdf",
      png: "mdi-file-image",
      txt: "mdi-file-document-outline",
      xls: "mdi-file-excel",
      xml: "mdi-file-document-outline"
    }
  }),
  methods: {
    itemSelected: function(item) {
      if (item[".xml"]) {
        store.state.selectedItem = item;
      }
    }
  }
};
</script>
