/* eslint-disable */
export default class File {
   constructor(data) {
      Object.keys(data).forEach(key=> {
         this[key] = data[key];
      })
   }

   static async fromURL(url) {
      const res = await fetch(url);
      const json = await res.json();
      const data = atob(json.content);
      return new File({data})
   }

}