<template>
  <v-app>
    <v-navigation-drawer permanent app class="neutral-1" width="420">
      <a @click="goHome">
        <v-list-item class="primary">
          <v-list-item-icon class="mr-1">
            <v-icon class="white--text" size="50">{{
              "mdi-note-minus-outline"
            }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title class="title white--text">
              {{ $store.state.repo ? $store.state.repo.repo : "" }}
            </v-list-item-title>
            <v-list-item-subtitle class="white--text font-weight-light">
              Documentation
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </a>

      <v-divider></v-divider>
      <SearchBar />
      <MetadataTree />
    </v-navigation-drawer>
    <v-main class="ma-3 neutral-1">
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>
import SearchBar from "./components/SearchBar";
import MetadataTree from "./components/MetadataTree";
import GitHubRepo from "./services/GitHubRepo";
import store from "./store";
export default {
  name: "App",
  gitRepo: null,
  components: {
    SearchBar,
    MetadataTree
  },
  store,
  async created() {
    if (!store.state.dev) {
      const res = await fetch("config.json");
      const config = await res.json();
      store.state.repo = await GitHubRepo.fromConfig(config);
    } else {
      store.state.repo = await GitHubRepo.fromConfig({
        username: "EncludeLtd",
        repo: "EasyGeocoder",
        branch: "main"
      });
    }
  },
  methods: {
    goHome: function() {
      store.state.selectedItem = null;
    }
  }
};
</script>
