<template>
  <v-card class="mx-auto pa-5" height="100%" outlined v-if="metadata">
    <v-list-item three-line style="max-width: 1200px;">
      <v-list-item-content>
        <v-list-item-title class="headline mb-1">
          {{ metadata.label || $store.state.selectedItem.name }}
        </v-list-item-title>
        <v-list-item-subtitle>
          {{ metadata.label ? metadata.fullName : "" }}
        </v-list-item-subtitle>

        <v-card-title class="mb-0 pb-2">Metadata</v-card-title>

        <v-simple-table dense>
          <template v-slot:default>
            <tbody>
              <tr v-for="item in metadata.items" :key="item.name">
                <td>
                  <DeCamelCase :text="item.name"></DeCamelCase>
                </td>
                <td>
                  <h4 class="font-weight-medium">
                    {{ item.content }}
                  </h4>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-list-item-content>
    </v-list-item>

    <v-divider></v-divider>
    <br />

    <Prism
      :title="$store.state.selectedItem['.xml'].name"
      language="xml"
      class="pa-5"
      >{{ metadata.raw }}</Prism
    >

    <v-divider></v-divider>
    <br />

    <Prism
      v-if="apexClass"
      :title="$store.state.selectedItem['.cls'].name"
      language="java"
      class="pa-5"
      >{{ apexClass.data }}</Prism
    >

    <!-- <v-card-actions>
      <v-btn outlined rounded text>
        Button
      </v-btn>
    </v-card-actions> -->
  </v-card>

  <v-card class="mx-auto pa-5" height="100%" outlined v-else>
    <vue-simple-markdown
      :source="$store.state.README.data"
    ></vue-simple-markdown>
  </v-card>
</template>

<script>
import store from "../store";
import Metadata from "../services/Metadata";
import File from "../services/File";
import "prismjs";
import "prismjs/components/prism-java";
import "prismjs/themes/prism.css";
import Prism from "vue-prism-component";
import DeCamelCase from "../components/DeCamelCase";
export default {
  name: "Home",
  components: {
    Prism,
    DeCamelCase
  },
  data: () => ({
    metadata: null,
    apexClass: null,
    fullName: "",
    label: ""
  }),
  store,
  watch: {
    "$store.state.selectedItem": async function() {
      this.metadata = null;
      this.apexClass = null;
      this.metadata = await Metadata.fromURL(
        store.state.selectedItem[".xml"].url
      );
      this.apexClass = store.state.selectedItem[".cls"]
        ? await File.fromURL(store.state.selectedItem[".cls"].url)
        : null;
    }
  }
};
</script>
