/* eslint-disable */
const parse = require('xml-parser');
export default class Metadata {
   constructor(data) {
      Object.keys(data).forEach(key=> {
         this[key] = data[key];
      })
   }

   static async fromURL(url) {
      const res = await fetch(url);
      const json = await res.json();
      const raw = atob(json.content);
      const parsed = parse(raw);

      const fullNameIdx = parsed.root.children.findIndex(obj => obj.name === "fullName");
      const fullName = fullNameIdx > -1 ? parsed.root.children.splice(fullNameIdx, 1)[0].content : null;
      const labelIdx = parsed.root.children.findIndex(obj => obj.name === "label");
      const label = labelIdx > -1 ? parsed.root.children.splice(labelIdx, 1)[0].content : null;
      const items = parsed.root.children
      const data = {
         raw,
         parsed,
         fullName,
         label,
         items
      }
      return new Metadata(data)
   }

}