/* eslint-disable */
import store from "../store";
import File from "./File";
export default class GitHubRepo {
   nodeSize = 0
   items = []
   constructor(config, files) {
      Object.keys(config).forEach(key=> {
         this[key] = config[key];
      })
      const fileExtRegex = new RegExp(/\.[0-9a-z]+$/i);
      files = files.filter(obj => fileExtRegex.test(obj.path));

      files.forEach(file => {
         const ext = file.path.match(fileExtRegex);
         if (!ext) return;
         this.createItems(this.items, file.path, ext, file.url)
      });
   }

   createItems(childItems, thisDir, ext, url) {
      let subsequentDirs = thisDir.split("/");
      let currentDir = subsequentDirs.shift();
      let existingDir = childItems.find(item => item.name === currentDir);
      if (existingDir) {
         this.createItems(existingDir.children, subsequentDirs.join("/"), ext, url);
         return;
      } 
      const item = {}
      if (subsequentDirs.length === 0) {
         let fileNameAndExtensions = currentDir.split(".")
         let fileName = fileNameAndExtensions.shift();
         let existingItem = childItems.find(item => item.name === fileName);
         if (existingItem) {
            existingItem[ext] = {
               name: currentDir,
               url: url
            };
            return;
         }
         item.name = fileName;
         item.id = this.newNodeId(item.name);
         item.endNode = true;
         item[ext] = {
            name: currentDir,
            url: url,
         }
         childItems.push(item)
      } else {
         item.name = currentDir;
         item.id = this.newNodeId(item.name);
         item.children = [];
         childItems.push(item);
         this.createItems(item.children, subsequentDirs.join("/"), ext, url);
      }
   }

   newNodeId(prefix) {
      prefix = prefix ? prefix : '';
      this.nodeSize++;
      return `${prefix} - ${this.nodeSize}`;
   }

   static async fromConfig(config) {
      // Get top repo's top level JSON
      const topURL = `https://api.github.com/repos/${config.username}/${config.repo}/git/trees/${config.branch}?recursive=1`;
      const topRes = await fetch(topURL);
      const topJSON = await topRes.json();

      // Get README.md and put in store
      const READMEJSON = topJSON.tree.find(child => child.path === "README.md");
      const README = await File.fromURL(READMEJSON.url);
      store.state.README = README;

      // Get force-app/main/default directory and all children; pass to constructor for parsing
      const defaultUrl = topJSON.tree.find(child => child.path === "force-app/main/default").url;
      const defaultRes = await fetch(defaultUrl + "?recursive=1");
      const defaultJSON = await defaultRes.json();
      return new GitHubRepo(config, defaultJSON.tree);
   }
}