<template>
  <v-sheet class="pa-4 primary lighten-2">
    <v-text-field
      v-model="searchTerm"
      label="Search Metadata"
      dark
      flat
      solo-inverted
      hide-details
      clearable
      prepend-inner-icon="mdi-magnify"
      clear-icon="mdi-close-circle-outline"
    ></v-text-field>
    <v-checkbox
      v-model="caseSensitive"
      dark
      hide-details
      label="Case sensitive search"
      v-on:change="caseSensitiveChange"
    ></v-checkbox>
  </v-sheet>
</template>

<script>
import store from "../store";
export default {
  data: () => ({
    search: null,
    caseSensitive: false
  }),
  store,
  methods: {
    caseSensitiveChange(isCaseSensitive) {
      store.state.searchFilter = isCaseSensitive
        ? (item, search, textKey) => {
            return item[textKey].indexOf(search) > -1;
          }
        : undefined;
    }
  },
  computed: {
    searchTerm: {
      get() {
        return store.state.searchTerm;
      },
      set(value) {
        store.state.treeOpen = true;
        store.state.searchTerm = value;
      }
    }
  }
};
</script>
